/* NavBar background color */
.navbar-custom {
    background-color: var(--color-primary);
}

/* The logo for the website in top left corner */
#web-logo {
    margin: auto;
    margin-right: 10%;
    cursor: pointer;
}

/* The container for the logo and the title */
.logo-and-title {
    margin: 0;
    padding-right: 5%;
    padding-left: 5%;
}

#basic-navbar-nav > div > a {
    color: var(--color-text);
}

#basic-navbar-nav > div > a:hover {
    color: var(--color-hover-text);
}